@import "../base/mixins.scss";
@import "../base/vars.scss";

.header {
  margin: 0 0 50px;
  padding: 33px 0 0;

  background-color: $color_blue_tangaroa;
  background-image: url("../img/circuit-desktop@1x.png"), url("../img/bg-header-desktop@1x.png"), url("../img/bg-header-delta@1x.png");
  background-repeat: no-repeat;
  background-position: top 71% right 14%, center, center;
  background-size: auto, cover, cover;

  @media (min-resolution: $retina_dpi) and (min-resolution: $retina_dppx) {
    background-color: $color_blue_tangaroa;
    background-image: url("../img/circuit-desktop@2x.png"), url("../img/bg-header-desktop@2x.png"), url("../img/bg-header-delta@2x.png");
    background-repeat: no-repeat;
    background-position: top 71% right 14%, center, center;
    background-size: auto, cover, cover;
  }

  @media (max-width: $width_viewport_tablet) {
    margin: 0 0 25px;
    padding: 20px 0 0;

    background-color: $color_blue_tangaroa;
    background-image: url("../img/circuit-tablet@1x.png"), url("../img/bg-header-desktop@1x.png"), url("../img/bg-header-delta@1x.png");
    background-repeat: no-repeat;
    background-position: top 32% right, center, center top -128px;
    background-size: auto, cover, cover;
  }

  @media (max-width: $width_viewport_tablet) and (min-resolution: $retina_dpi) and (min-resolution: $retina_dppx) {
    background-color: $color_blue_tangaroa;
    background-image: url("../img/circuit-tablet@2x.png"), url("../img/bg-header-desktop@2x.png"), url("../img/bg-header-delta@2x.png");
    background-repeat: no-repeat;
    background-position: top 32% right, center, center top -128px;
    background-size: 34%, cover, cover;
  }

  @media (max-width: $width_viewport_mobile) {
    margin: 0 0 17px;
    padding: 17px 0 0;

    background-color: $color_blue_tangaroa;
    background-image: url("../img/circuit-mobile@1x.png"), url("../img/bg-header-mobile@1x.png"), url("../img/bg-header-delta@1x.png");
    background-repeat: no-repeat;
    background-position: top 21% right, center, right 45% top -134px;
    background-size: auto, cover, 450%;
  }

  @media (max-width: $width_viewport_mobile) and (min-resolution: $retina_dpi) and (min-resolution: $retina_dppx) {
    background-color: $color_blue_tangaroa;
    background-image: url("../img/circuit-mobile@2x.png"), url("../img/bg-header-mobile@2x.png"), url("../img/bg-header-delta@2x.png");
    background-repeat: no-repeat;
    background-position: top 22% right, center, right 45% top -134px;
    background-size: 92%, cover, 450%;
  }
}

.header__inner {
  @include inner_wide;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__logo-link {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 52px;
  margin: 0 0 0 5px;

  &:focus {
    outline: none;
  }

  @media (max-width: $width_viewport_tablet) {
    margin: 0;
  }

  @media (max-width: $width_viewport_mobile) {
    width: 38px;
  }
}

.header__logo-link:focus > .header__logo-text {
  color: $color_blue_sky;
}

.header__logo-link:focus > .header__logo-slogan {
  color: $color_blue_sky;
}

.header__logo-text {
  @include indent_p;

  margin: 0;
  padding: 5px 0 0 16px;

  font-weight: $font_weight_bold;
  font-size: $font_size_p_desktop_xl;
  line-height: $line_height_p_desktop_l;
  font-family: $font_family_second_base;
  color: $color_white;

  &:hover {
    color: $color_blue_sky;
  }

  @media (max-width: $width_viewport_tablet) {
    font-size: $font_size_p_tablet_xl;
    line-height: $line_height_p_tablet_xxs;
  }

  @media (max-width: $width_viewport_mobile) {
    display: none;
  }
}

.header__logo-slogan {
  @include indent_p;

  margin: 0;
  padding: 0 0 0 16px;

  font-weight: $font_weight_medium;
  font-size: $font_size_p_desktop_s;
  line-height: $line_height_p_desktop_l;
  font-family: $font_family_third_base;
  color: $color_gray_light;

  &:hover {
    color: $color_blue_sky;
  }

  @media (max-width: $width_viewport_tablet) {
    font-size: $font_size_p_tablet_s;
    line-height: $line_height_p_tablet_xxs;
  }

  @media (max-width: $width_viewport_mobile) {
    display: none;
  }
}

.header__text-product {
  @include indent_p;

  max-width: 50%;
  margin: 0 0 31px;

  font-weight: $font_weight_bold;
  font-size: $font_size_p_desktop_xxl;
  line-height: $line_height_p_desktop_xl;
  font-family: $font_family_second_base;
  color: $color_white;
  letter-spacing: 0.07em;

  @media (max-width: $width_viewport_tablet) {
    margin: 0 0 33px;

    font-size: $font_size_p_tablet_xxl;
    line-height: $line_height_p_tablet_xxs;
    text-transform: uppercase;
  }

  @media (max-width: $width_viewport_mobile) {
    max-width: 250px;
    margin: 0 0 28px;

    font-size: $font_size_p_mobile_xxl;
    line-height: $line_height_p_mobile_xxxs;
  }
}

.header__text-services {
  @include indent_p;

  position: relative;

  max-width: 50%;
  margin: 0;
  padding: 0 0 44px 98px;

  font-size: $font_size_p_desktop_l;
  line-height: $line_height_p_desktop_xxxl;
  color: $color_white;

  @media (max-width: $width_viewport_tablet) {
    max-width: 60%;
    padding: 0 0 56px 50px;

    font-size: $font_size_p_tablet_l;
    line-height: $line_height_p_tablet_xl;
  }

  @media (max-width: $width_viewport_mobile) {
    max-width: 100%;
    padding: 0 0 43px 50px;

    font-size: $font_size_p_mobile;
    line-height: $line_height_p_mobile_xl;
  }

  &::before {
    content: "";
    position: absolute;
    top: 16px;
    left: 4px;

    width: 66px;
    height: 2px;

    background: $color_white;

    @media (max-width: $width_viewport_tablet) {
      top: 15px;
      left: 3px;

      width: 33px;
      height: 2px;
    }
  }
}

.header__content {
  position: relative;

  display: flex;
  flex-basis: 100%;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 228px 0 275px;
  overflow: initial;

  @media (max-width: $width_viewport_tablet) {
    margin: 0 0 0 63px;
    padding: 115px 0 163px;
  }

  @media (max-width: $width_viewport_mobile) {
    margin: 0;
    padding: 230px 0 70px;
  }
}
